html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100vh;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Playfair Display", serif;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2.5rem;
}

h4 {
  font-size: 2rem;
}

/* Layout */
.header__pc {
  display: none;
}

.header__mobile-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  border-bottom: 1px solid white;
  max-height: 20rem;
  background-color: #0f0f0e;
}

.header__mobile-logo {
  width: 100px;
}

@media screen and (min-width: 310px) {
  .header__mobile-logo {
    width: 150px;
  }
}
.header__mobile-hmenu {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  z-index: 9999;
  border: none;
  background-color: transparent;
}

.header__mobile-hmenu-line {
  background-color: white;
  width: 32px;
  height: 4px;
  position: relative;
}

@media screen and (min-width: 600px) {
  .header__mobile-logo {
    width: 150px;
  }
}
@media screen and (min-width: 920px) {
  .header__mobile-logo {
    width: 200px;
  }
}
@media screen and (min-width: 1100px) {
  .header__mobile-head {
    height: 20rem;
  }
}
.header__mobile-overlay {
  height: 100vh;
  width: 100vw;
  background-color: #0f0f0e;
  z-index: 5;
  position: absolute;
  display: flex;
  flex-direction: column;
  left: -100%;
  display: none;
}
.header__mobile-overlay a {
  color: white;
  border-bottom: 1px solid rgba(255, 255, 255, 0.26);
  font-size: 1.6rem;
}

.header__mobile-overlay-head {
  align-items: center;
  display: flex;
  flex-basis: 15rem;
  justify-content: flex-start;
  padding: 2rem;
  gap: 2.5rem;
}
.header__mobile-overlay-head a {
  border-bottom: none;
}

.header__mobile-head-contenedor-botones {
  display: flex;
  align-items: center;
}

.header__mobile-overlay-head-user {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  gap: 1rem;
}

.header__mobile-head-perfil-cont {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 310px) {
  .header__mobile-head-perfil-cont {
    width: 10rem;
  }
}
.header__mobile-head-button-perfil {
  background-color: transparent;
  height: 4rem;
  border: none;
  border-radius: 10px;
}
.header__mobile-head-button-perfil:hover {
  cursor: pointer;
}
.header__mobile-head-button-perfil img {
  height: 100%;
}

.header__mobile-head-button-perfil-c {
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  padding: 0rem 4rem;
  position: absolute;
  background: #0f0f0e;
  width: max-content;
  justify-content: center;
  top: 7rem;
}
.header__mobile-head-button-perfil-c a {
  color: white;
  display: none;
  opacity: 0;
  font-size: 1.9rem;
}

.header__mobile-overlay-body {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header__mobile-overlay-body ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.header__mobile-overlay-body-a {
  opacity: 0;
  position: relative;
  letter-spacing: 0px;
  left: -20px;
  bottom: -10px;
}

.header__mobile-overlay-footer {
  flex-basis: 15rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

@media screen and (min-width: 999px) {
  .header__mobile {
    display: none;
  }
  .header__pc {
    display: flex;
    background-color: #0f0f0e;
    padding: 1.5rem;
    justify-content: space-between;
    border-bottom: 1px solid white;
  }
  .header__pc-logo {
    margin-left: 5rem;
  }
  .header__pc-nav-div {
    display: flex;
    width: 75%;
    align-items: center;
    margin-right: 5rem;
  }
  .header__pc-nav {
    width: 100%;
  }
  .header__pc-nav-ul {
    display: flex;
    list-style: none;
    justify-content: space-around;
  }
  .header__pc-nav-ul li a {
    color: white;
    font-size: 1.8rem;
    position: relative;
    letter-spacing: 0px;
  }
  .header__pc-nav-ul li a::after {
    content: "";
    display: block;
    position: absolute;
    width: 0%;
    height: 1px;
    background-color: rgb(255, 255, 255);
    transition: width 0.2s ease-in-out, background-color 0.75s ease;
  }
  .header__pc-nav-ul li a:hover::after {
    background-color: rgb(110, 110, 110);
    width: 100%;
  }
}
@media screen and (min-width: 1400px) {
  .header__pc-nav-div {
    width: 55%;
  }
}
footer {
  height: 10rem;
  background-color: rgb(15, 15, 14);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  position: relative;
}

footer::after {
  width: 95%;
  content: "";
  height: 1px;
  background-color: rgba(160, 160, 160, 0.123);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.footer-socials {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.footer-socials-icon {
  width: 28px;
  height: 28px;
}

.footer-avisos {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
}
.footer-avisos a {
  color: white;
  border-bottom: 1px solid rgb(170, 170, 170);
}

/* Pages */
.main-index {
  display: flex;
  flex: 1;
  overflow: hidden;
}

.main-index-img {
  width: 100vw;
  border: 1px solid black;
  object-position: center center;
  object-fit: cover;
}

@media screen and (min-width: 500px) {
  .main-index-img {
    width: 50vw;
  }
}
@media screen and (min-width: 999px) {
  .main-index-img {
    width: 25vw;
  }
}
.main-galeria {
  background-color: rgb(19, 18, 18);
  flex: 1;
  padding-bottom: 5rem;
}

.main-galeria-h1 {
  color: white;
  text-align: center;
  text-shadow: -1px 2px rgba(138, 138, 138, 0.288);
}

.main-galeria-grid {
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.main-galeria-grid-cat {
  width: 85%;
  margin: 0 auto;
  border: 1px solid rgba(255, 255, 255, 0.062);
  padding: 1rem;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}
.main-galeria-grid-cat h2 a {
  font-size: 1.9rem;
  color: rgb(182, 182, 182);
  border-bottom: 1px solid grey;
  text-shadow: -1px 2px rgba(2, 2, 2, 0.514);
  text-align: center;
  display: block;
}
.main-galeria-grid-cat > a {
  flex: 1;
}
.main-galeria-grid-cat img {
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
  box-shadow: -2px 2px rgba(0, 0, 0, 0.514);
}

.main-galeria-category {
  background-color: #131212;
  flex: 1;
  padding-bottom: 4rem;
}

.main-galeria-category-head {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.main-galeria-category-head h1 {
  color: #fff;
  text-align: center;
  text-shadow: -1px 2px hsla(0, 0%, 54%, 0.288);
}
.main-galeria-category-head a {
  border-radius: 10px;
  padding: 1rem;
  color: white;
  background-color: #0e0d0d;
  font-weight: bold;
  border: 1px solid rgb(255, 255, 255);
}

.main-galeria-category-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  width: 95%;
  margin: 0 auto;
}

.main-galeria-category-grid-cat {
  border: 1px solid hsla(0, 0%, 100%, 0.062);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0.1rem;
  width: 95%;
  object-fit: cover;
}
.main-galeria-category-grid-cat a, .main-galeria-category-grid-cat img {
  height: 100%;
}
.main-galeria-category-grid-cat img {
  border-radius: 10px;
}

@media screen and (min-width: 800px) {
  .main-galeria-category-grid {
    width: 85%;
  }
}
.main-clases {
  flex: 1;
  background-color: #131212;
  color: white;
}

.main-clases-head h1 {
  text-shadow: -1px 2px rgba(138, 138, 138, 0.288);
  text-align: center;
}

.main-clases-body {
  width: 90%;
  margin: 0 auto;
}
.main-clases-body h2 {
  display: inline;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid grey;
  font-size: 1.8rem;
}
.main-clases-body p, .main-clases-body ul {
  font-size: 1.5rem;
}

.main-clases-body-quest {
  margin: 5rem 0;
}
.main-clases-body-quest a {
  color: white;
  border-bottom: 1px solid rgb(77, 77, 77);
}

@media screen and (min-width: 800px) {
  .main-clases-body h2 {
    font-size: 2.2rem;
  }
  .main-clases-body p, .main-clases-body ul {
    font-size: 1.9rem;
  }
}
.main-sesion {
  flex: 1;
  background-color: #131212;
  color: white;
}

.main-sesion-head h1 {
  text-shadow: -1px 2px rgba(138, 138, 138, 0.288);
  text-align: center;
}

.main-sesion-body {
  width: 90%;
  margin: 0 auto;
}
.main-sesion-body h2 {
  display: inline;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid grey;
  font-size: 1.8rem;
}
.main-sesion-body p {
  font-size: 1.5rem;
}

.main-sesion-body-quest {
  margin: 5rem 0;
}
.main-sesion-body-quest a {
  color: white;
  border-bottom: 1px solid rgb(77, 77, 77);
}

@media screen and (min-width: 800px) {
  .main-sesion-body h2 {
    font-size: 2.2rem;
  }
  .main-sesion-body p, .main-sesion-body ul {
    font-size: 1.9rem;
  }
}
.main-contacto {
  flex: 1;
  background-color: #131212;
  color: white;
}

.main-contacto-head h1 {
  text-shadow: -1px 2px rgba(138, 138, 138, 0.288);
  text-align: center;
}

.main-contacto-body {
  width: 90%;
  margin: 0 auto;
}
.main-contacto-body h2 {
  display: inline;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid grey;
  font-size: 1.8rem;
}
.main-contacto-body p, .main-contacto-body ul {
  font-size: 1.5rem;
}
.main-contacto-body a {
  border-bottom: 1px solid rgba(128, 126, 126, 0.493);
  color: white;
}

@media screen and (min-width: 800px) {
  .main-contacto-body h2 {
    font-size: 2.2rem;
  }
  .main-contacto-body p, .main-contacto-body ul {
    font-size: 1.9rem;
  }
}
.main-login {
  background-color: #131212;
  flex: 1;
  display: grid;
  place-items: center;
}

.main-login-form-c {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.85);
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.main-login-form-c .main-login-form {
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
.main-login-form-c .main-login-form-field {
  position: relative;
  display: flex;
  align-items: center;
}
.main-login-form-c .main-login-form-field input {
  border: none;
  padding: 1rem;
  color: #131212;
  background-color: transparent;
  border-bottom: 1px solid #131212;
}
.main-login-form-c .main-login-form-field label {
  font-weight: bold;
  color: #131212;
  position: absolute;
  left: 1rem;
  bottom: 0.5rem;
  transition: all 0.5s ease;
}
.main-login-form-c .main-login-form-a {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-bottom: 5rem;
}
.main-login-form-c .main-login-form-a a {
  color: #131212;
  border-bottom: 1px solid grey;
  font-weight: bold;
}
.main-login-form-c input[type=submit] {
  background-color: rgba(255, 255, 255, 0.85);
  border: 1px solid #131212;
  padding: 1rem;
  border-radius: 20px;
}

.main-login-form-field input:focus,
.main-login-form-field input:focus-visible {
  outline: none;
}

.main-login-form-field input:focus ~ label,
.main-login-form-field input:valid ~ label {
  font-size: 1.1rem;
  bottom: 3rem;
  color: #131212;
}

/* Admin */
.main__admin {
  flex: 1;
  background-color: #131212;
  display: flex;
  flex-direction: column;
}

.main__admin-head {
  color: white;
  text-align: center;
  font-size: 1.8rem;
  width: 90%;
  margin: 0 auto;
}

.main__admin-body {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1.5rem;
}
.main__admin-body h2 {
  font-size: 1.8rem;
  text-align: center;
}
.main__admin-body a, .main__admin-body h2 {
  color: white;
}

.main__admin-body-a {
  display: flex;
  gap: 3rem;
  justify-content: center;
}
.main__admin-body-a a {
  border-radius: 15px;
  margin: 0;
  text-align: center;
  background-color: #0f0f0e;
  border: 1px solid rgb(196, 193, 193);
  padding: 1.5rem;
  letter-spacing: 3px;
}

.main__admin-g {
  flex: 1;
  background-color: #131212;
}

.main__admin-g-head {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 90%;
  margin: 2rem auto;
}
.main__admin-g-head a, .main__admin-g-head h1 {
  color: white;
}
.main__admin-g-head a {
  padding: 1rem;
  border: 1px solid wheat;
  border-radius: 10px;
}

.main__admin-g-body {
  width: 90%;
  margin: 0 auto;
}

.main__admin-g-body-head {
  margin: 3rem 0;
  display: flex;
  justify-content: flex-end;
}
.main__admin-g-body-head a {
  color: white;
  padding: 1.5rem;
  border-radius: 10px;
  border: 1px solid white;
}

.main__admin-g-body-body {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-bottom: 5rem;
}

.main__admin-g-body-body-c {
  border: 1px solid grey;
  border-radius: 10px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
}
.main__admin-g-body-body-c h2 {
  text-align: center;
}
.main__admin-g-body-body-c h2 a {
  font-size: 2.5rem;
  color: white;
  border-bottom: 1px solid grey;
}
.main__admin-g-body-body-c > a {
  flex: 1;
}
.main__admin-g-body-body-c img {
  object-fit: cover;
  height: 100%;
